import Vue from 'vue'
import Router from 'vue-router'
import Index from './views/Index'
import Result from './views/Result'
import Error404 from './views/Error404'
import ResultText from './views/ResultText'

import $store from './store'
import {mutations} from './store'
import moment from 'moment'

Vue.use(Router)

// TODO (17.11.18) name constants
const router = new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path: '/result',
      name: 'result',
      component: Result,
      // DataForm
      meta: {requiresData: true}
    },
    {
      path: '/result-text',
      name: 'result-text',
      component: ResultText,
      meta: {requiresData: true}
    },
    {
      path: '/about',
      name: 'about',
      // route level code-splitting
      // this generates a separate chunk (about.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import(/* webpackChunkName: "about" */ './views/About.vue')
    },
    {path: '*', component: Error404}
  ]
})
function queryValid (query) {
  // TODO (18.11.18) return result obj {packAmount: {value: 6, valid: true}, ...}
  // if (Object.keys(query).length !== 4) return false
  // TODO (16.11.18) validate numbers
  if (!((query.hasOwnProperty('lastCigarette')) &&
    query.hasOwnProperty('packPrice') &&
    query.hasOwnProperty('packAmount') &&
    query.hasOwnProperty('cigarettesPerDay'))) return false
  if (!moment(query.lastCigarette).isSameOrBefore(moment())) return false
  if (!moment(query.lastCigarette).isValid()) return false
  return true
}
function queryPresent (query) {
  const size = Object.keys(query).length
  return size > 0
}
// TODO (16.11.18) include in new...
router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresData)) {
    console.log(`${to.name} requires data`)
    if (queryPresent(to.query)) {
      if (queryValid(to.query)) {
        console.log(`valid query (${JSON.stringify(to.query)})`)
        // sync with $store (and DataForm)
        $store.dispatch(
          mutations.SUBMIT_DATA_FORM,
          to.query
        )
      } else {
        console.warn(`invalid query (${JSON.stringify(to.query)})`)
        // show the form
        $store.commit(mutations.DATA_FORM_ACTIVATE)
      }
    } else {
      console.warn('requires data but no data present')
      // show the form
      $store.commit(mutations.DATA_FORM_ACTIVATE)
    }
  } else {
    console.warn('nothing to do here')
  }
  next()
})

export default router
