<template>
  <modal v-if="$store.state.dataFormActive" @close="$emit('close')" id="data-form">
    <h3 slot="header">{{ $t('form.header') }}</h3>
    <div slot="body">
      <div>
        <form @submit.prevent="submitDataForm">
          <div class="form-group">
            <div class="form-group">
              <label for="last-cigarette">Letzte Zigarette</label>
              <!--TODO: language-->
              <datepicker
                  v-model="lastCigaretteDate"
                  id="last-cigarette"
                  :bootstrap-styling="true"
              >
              </datepicker>
            </div>
            <label for="cigarettes-per-day">{{ $t('form.label.cigarettesPerDay') }}</label>
            <input
                v-model.number="cigarettesPerDay"
                :placeholder="$t('form.placeholder.cigarettesPerDay')"
                id="cigarettes-per-day"
                class="form-control"
                name="cigarettes-per-day"
                type="number"
                min="1"
            >
            <small class="form-text text-muted">{{$t('form.help.cigarettesPerDay')}}</small>
          </div>
          <div class="form-group">
            <label for="pack-price">{{ $t('form.label.packPrice') }}</label>
            <input
                v-model.number="packPrice"
                :placeholder="$t('form.placeholder.packPrice')"
                id="pack-price"
                class="form-control"
                name="pack-price"
                type="number"
                :lang="$i18n.locale"
                min="0.1"
                step="0.1"
            >
          </div>
          <div class="form-group">
            <label for="pack-amount">{{ $t('form.label.packAmount') }}</label>
            <input
                v-model.number="packAmount"
                :placeholder="$t('form.placeholder.packAmount')"
                id="pack-amount"
                class="form-control"
                name="pack-amount"
                type="number"
                min="1"
            >
          </div>
        </form>
      </div>
    </div>
    <div slot="footer">
      <button
          @click="$emit('close')"
          type="button"
          class="btn btn-outline-danger mx-1"
      >
        {{ $t('form.abort') }}
      </button>
      <!--// TODO (17.11.18) fix submit-->
      <!--// TODO (17.11.18) validate form-->
      <!--berechnen-->
      <button
          @click.prevent="submitDataForm"
          type="submit"
          class="btn btn-outline-primary mx-1"
      >
        {{ $t('form.submit') }}
      </button>
    </div>
  </modal>
</template>
<script>
import moment from 'moment'
import {mutations} from '../store'
import Datepicker from 'vuejs-datepicker'
import Modal from './Modal'

export default {
  name: 'DataForm',
  components: {
    Datepicker,
    Modal
  },
  data: function () {
    return {
      lastCigaretteDate: moment('2018-11-03').toDate(),
      cigarettesPerDay: 16,
      packPrice: 6.4,
      packAmount: 21,
      formFields: [
        {
          mutation: mutations.LAST_CIGARETTE_SET,
          key: 'lastCigaretteDate'
        },
        {
          mutation: mutations.PACK_PRICE_SET,
          key: 'packPrice'
        },
        {
          mutation: mutations.PACK_AMOUNT_SET,
          key: 'packAmount'
        },
        {
          mutation: mutations.CIGARETTES_PER_DAY_SET,
          key: 'cigarettesPerDay'
        }
      ]
    }
  },
  methods: {
    // initializes $store.{prop} this.{prop} $route.query.{prop}
    initFormFields () {
      const q = {}
      this.formFields.forEach(x => {
        // $route.query is the source of truth if set: -> update data property
        if (this.$route.query.hasOwnProperty(x.key)) this[x.key] = this.$route.query[x.key]
        // TODO: use action
        this.$store.commit(x.mutation, this[x.key])
        // q[x.key] = this[x.key]
      })
      this.$router.push({ name: this.$route.name, query: q })
      // this.lastCigaretteDate = moment(this.lastCigarette).toDate()
    },
    submitDataForm () {
      // TODO (16.11.18) Destructuring?
      const lastCigarette = moment(this.lastCigaretteDate).format('YYYY-MM-DD')
      // TODO (16.11.18) action constant
      this.$store.dispatch(
        mutations.SUBMIT_DATA_FORM,
        {
          lastCigarette: lastCigarette,
          packPrice: this.packPrice,
          packAmount: this.packAmount,
          cigarettesPerDay: this.cigarettesPerDay
        }
      )

      this.$router.push({
        name: 'result',
        query: {
          lastCigarette: lastCigarette,
          packPrice: this.packPrice,
          packAmount: this.packAmount,
          cigarettesPerDay: this.cigarettesPerDay
        }
      })

      this.$store.commit(mutations.DATA_FORM_DEACTIVATE)
    },
    syncWithRoute () {
      // sync with $route
      console.group('DataForm syncs with $route')
      for (let [key, value] of Object.entries(this.$route.query)) {
        console.log(`syncing: ${key}: ${value}`)
        if (key === 'lastCigarette') this.lastCigaretteDate = moment(value).toDate()
        else this[key] = value
      }
      console.groupEnd()
    }
  },
  created () {
    this.syncWithRoute()
  }
}
</script>

