<template>
  <div id="result-text">
    <h1>{{$t('successMsg.header')}}</h1>
    <div class="alert alert-success" role="alert">
      <i18n path="successMsg.text" tag="p">
        <span place="daysSmokeFree" class="hl-number">{{$store.getters.daysSmokeFree}}</span>
        <span place="moneySaved" class="hl-number">{{$store.getters.moneySaved | formatTwoDigits}}</span>
        <span place="cigarettesSaved" class="hl-number">{{$store.getters.cigarettesSaved | toInt}}</span>
      </i18n>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ResultText',
  methods: {
  },
  filters: {
    formatTwoDigits: function (value) {
      const digits = 2
      const power = Math.pow(10, digits)
      return (Math.round(value * power) / power).toFixed(digits)
    },
    toInt (value) {
      return Math.round(value)
    }
  }
}
</script>

<style scoped>
  .hl-number {
    color: #DE1B2E;
  }
</style>